<style>
	.xingcheng_nav{ position: relative; border-bottom: 1px solid #f6f6f6;}
	.xingcheng_nav ul{
		margin:0; padding: 0;
	   background: #fff; padding: 1rem 0rem;display: flex; display: -webkit-flex;
	   justify-content: center;align-items: center;
	   
	}
	.xingcheng_nav ul li{ font-size:1.4rem;flex:auto; text-align: center; border-left:1px solid #ddd}
	.xingcheng_nav ul li:first-child{ border-left:none}
	.xingcheng_nav ul .navactive{ color: #FF414F;}
	.xingcheng_nav label{
		transition:0.2s;
		 width:13.3333%; position: absolute; left:10%; bottom:0px; height: 2px; background: url(../assets/bj.jpg);}

    .xingcheng_content .content{ padding-bottom: 3rem;}
    .xingcheng_content .activecontent{ display: block !important;}
    
    .wait{  text-align: center; padding: 1rem 0rem;}
    .wait img{ width:7rem ;height: auto; display: inline-block; margin: 0 auto;}
    .wrapper ul { padding: 0rem 1rem; height: 100%;}
    .wrapper ul li{ height: 100px; line-height: 100px; background: #fff; margin-top: 0.5rem;}

    .none{ height: 100%; position: relative;}
    .none img{ position: absolute; left: 0px; right: 0px; width:150px; margin: auto; top:50%; transform:translateY(-50%); -webkit-transform:translateY(-50%);  }
</style>
<template>
	<div class="xingcheng">
		<div class="xingcheng_nav">
			<ul @click="navClick">
				<li data-id="1" class="navactive"></li>
			    <li data-id="2"></li>
			    <li data-id="3"></li>
			</ul>
			<label ref="moveLable"></label>
		</div>
		
			<div class="wrapper xingcheng_content" ref="wrapper" style="; height:calc(100vh - 7rem); overflow:hidden;">
				  <ul  ref="ref1" data-id="1" class="content activecontent">
					<!-- <li v-for="item in splist">{{item.xm}}</li>-->
					
				  </ul>
			  <!--  DOM -->
			</div>
	</div>
</template>

<script>
import BScroll from 'better-scroll'
import $ from "jquery"
export default {
	name:"xingcheng",
	 components: {  },
	data(){
		return{
			splist:[],
			splist1:[{"xm":"1"},{"xm":"1"},{"xm":"1"},{"xm":"1"},{"xm":"1"},{"xm":"1"},{"xm":"1"},{"xm":"1"},{"xm":"1"},{"xm":"1"},{"xm":"1"}],
			splist2:[{"xm":2},{"xm":2},{"xm":2},{"xm":2},{"xm":2},{"xm":2},{"xm":2},{"xm":2},{"xm":2},{"xm":2},{"xm":2},{"xm":2},{"xm":2},{"xm":2}],
			splist3:[{"xm":3},{"xm":3},{"xm":3},{"xm":3},{"xm":3},{"xm":3},{"xm":3},{"xm":3},{"xm":3},{"xm":3},{"xm":3},{"xm":3},{"xm":3},{"xm":3},{"xm":3}],
		}
	},
	methods:{
		navClick(event){
			$(".activecontent").animate({scrollTop:0}, 500);
			var o=event.target;
			if(o.nodeName!="LI"){return;}
			var did=o.getAttribute("data-id");
			var objs=document.querySelectorAll(".xingcheng_nav ul li");
			for(let i=0;i<objs.length;i++)
			{
				objs[i].setAttribute("class","")
			}
			o.setAttribute("class","navactive")
			if(did=="1"){
				this.$refs.moveLable.style.left="10%"
				this.splist=this.splist1;
			}
			if(did=="2"){
				this.$refs.moveLable.style.left="43.3%"
				this.splist=this.splist2;
			}
			if(did=="3"){
				this.$refs.moveLable.style.left="76.6%"
				this.splist=this.splist3
			}
			$(".wait").remove();
		},
		
		_createWait(){
			$(".wait").remove();
			var div=document.createElement("div")
			div.setAttribute("class","wait");
			var img=document.createElement("img")
			img.src="../../static/img/wait2.gif";
			div.appendChild(img);
			return div;
		},
		_createEnd(){
			var div=document.createElement("div");
			div.innerHTML="";
			return div;
		},
		_createNone(){
			var div=document.createElement("div");
			div.setAttribute("class","none");
			var img=document.createElement("img")
			img.src="../../static/img/none.png";
			div.appendChild(img);
			return div;
		}
	},
	watch:{
		splist:function(){
			 this.$nextTick(function(){
			 	this.scroll.scrollTo(0, 0, 300)
			 })
		}
	},
	mounted(){
		//$(".xingcheng_nav ul li:first-child").click()
		//this.split=this.splist1;
		this.$nextTick(()=>{
		   this.scroll = new BScroll(this.$refs.wrapper, {})
	            this.scroll.on('scrollEnd', (pos) => {
			    if(pos.y==this.scroll.maxScrollY)
				{
					var o=this._createWait();
					this.$refs.ref1.appendChild(o)
					this.scroll.refresh()
				}
				    console.log(this.scroll.maxScrollY)
				     console.log(pos)
				})
		})
	},
   
};
</script>




